import { ProfileService } from './../services/profile.service';
import { Component, OnInit } from '@angular/core';
import { RouterConstants } from 'src/app/shared/config/constants/router.constants';
import { GlobalDataService } from 'src/app/shared/services/globalData/global-data.service';
import { Router } from '@angular/router';
import { Order, OrderState } from 'src/app/shared/models/profile.model';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-order-list',
  templateUrl: './order-list.page.html',
  styleUrls: ['./order-list.page.scss'],
})
export class OrderListPage implements OnInit {

  RouterConstants = RouterConstants;

  elements: Array<Order> = new Array<Order>();
  totalElements: number;
  loadingContent: boolean;
  currentElement: number;
  elementsPerPage: number = 10;

  environment = environment;
  constructor(
    public globalData: GlobalDataService,
    private profileService: ProfileService,
    private router: Router,
  ) { }

  ngOnInit() {
    this.getOrderList();
  }

  array(n: number): any[]{
    return Array(n);
  }

  async getOrderList(event?) {
    try {
      if (!event) this.loadingContent = true;
      this.currentElement = this.elements.length;
      const data = await this.profileService.getOrderListUser(this.currentElement, this.elementsPerPage).toPromise();
      this.totalElements = data.count;
      this.elements = this.elements.concat(data.rows);
      console.log('Prizes ', this.elements, this.totalElements);
      if (event) { await event.target.complete(); }
      this.loadingContent = false;
    } catch (error) {
      console.log(error);
      this.loadingContent = false;
    }
  }

  async refreshData(event) {
    this.elements = new Array<Order>();
    await this.getOrderList()
    event.target.complete();
  }

  async openDetail(element) {
    console.log('Detail', element);
    this.router.navigateByUrl(RouterConstants.PROFILE_ORDERS_DETAIL+ element.id, {state: element.selectedElement});
    //await this.alertService.showBasicAlert('No se ha podido recibir la recompensa!','Lo sentimos, la recompensa no est√° disponible en este momento.');
  }

  dateToString(date: Date) {
    return (("0" + (date.getDate())).slice(-2)) + "/" + (("0" + (date.getMonth() + 1)).slice(-2)) + "/" + date.getFullYear();
  }

  setDotColorState(state: string): string {
    let color: string = '#bbb';
    switch (state){
      case OrderState.REQUESTED:
        color = '#297fb9';
        break;
      case OrderState.OBTAINED:
        color = '#50c02b';
        break;
      case OrderState.REJECTED:
        color = '#c0392b';
        break;
    }
    return color;
  }

}
